
import { defineComponent, reactive, toRefs, defineAsyncComponent, inject, onBeforeMount, onMounted } from 'vue'
import Loading from "@/utils/loading";
import civil from "@/components/legal/civil.vue";
export default defineComponent({
  props: {
    columns: {
      type: Array,
      default: []
    },
    item: {
      type: Array,
      default: []
    },
    acitve: {
      type: Number,
      default: 0
    },
    index:{
      type: Number,
      default: 0
    }
  },
  setup() {
    const { loading } = Loading()
    const $api :any = inject('$api')
    const dataMap = reactive({
      itemList: [],
      source: {
        BusinessPeriod:[],
        onlyCode:"",
        registrationId:"",
        name:"",
        corporateRepresentative:"",
        registeredCapital:"",
        totalInvestment:"",
        totalInveCheck:"",
        enterpriseType:"",
        registrationDate:"",
        businessPeriodType:9901,
        businessPeriod:"",
        currentStatus:"",
        registrationAuthority:"",
        registrationAddress:"",
        businessScope:"",
        annualreport:"",
        superiorAuthority:"",
        organizationCode:"",
        comment:"",
        TotalUnit:"",
        TotalType:"",
        CurrencyUnit:"",
        CurrencyType:"",


        onlyCodeLabel:"统一社会信用代码",
        registrationIdLabel:"注册号",
        nameLabel:"名称",
        corporateRepresentativeLabel:"法定代表人",
        registeredCapitalLabel:"注册资本",
        TotalUnitLabel:"单位",
        TotalTypeLabel:"币种",
        totalInvestmentLabel:"投资总额",
        enterpriseTypeLabel:"类型",
        registrationDateLabel:"成立日期",
        businessPeriodTypeLabel:"经营期限",
        currentStatusLabel:"登记状态",
        registrationAuthorityLabel:"登记机关",
        registrationAddressLabel:"注册地址",
        businessScopeLabel:"经营范围",
        annualreportLabel:"年度报告",
        superiorAuthorityLabel:"上级/业务主管单位",
        organizationCodeLabel:"组织机构代码",
        commentLabel:"备注",
      },
      params: {
        enterpriseId: '602698853'
      },
      dictObj: {
        PersonTypeList: Array(),
        UnitType:  Array(),
        MoneyType: Array(),
        DateTypeList: Array(),
        EnterpriseTypeList: Array(),
        BusinessPeriodList: Array(),
        CurrentStatusList: Array(),
        ResultList: [],
        EnglishNameList: [],
        EnglishSourceList: [],
        PhoneSourceList: [],
        ResultTypeList: []
      },//获取字典字段
      dictionariesParams: {
        dType: '98,46,60,97,99,87,10,133,134,135,136,100'
      },
      dialogVisible: false
    })

    //接口加载注册信息数据
    const getRegisterInfo = async () => {
      const res: any = await $api.report.getEnterpriseRegisterInfo(dataMap.params)
      dataMap.itemList = res.data
    }
    //获取数据字典
    const getDictionary = async() => {
      const res: any =  await $api.dict.getDict(dataMap.dictionariesParams.dType)
      if (res.code === 200) {
        res.data.forEach((e: any) => {
          if (e.Dtype === '98') {
            dataMap.dictObj.PersonTypeList = e.DResult;
          } else if (e.Dtype === '46') {
            dataMap.dictObj.UnitType = e.DResult;
          } else if (e.Dtype === '60') {
            dataMap.dictObj.MoneyType = e.DResult;
          } else if (e.Dtype === '97') {
            dataMap.dictObj.DateTypeList = e.DResult;
          } else if (e.Dtype === '10') {
            dataMap.dictObj.EnterpriseTypeList = e.DResult;
          } else if (e.Dtype === '99') {
            dataMap.dictObj.BusinessPeriodList = e.DResult;
          } else if (e.Dtype === '87') {
            dataMap.dictObj.CurrentStatusList = e.DResult;
          } else if (e.Dtype === '133') {
            dataMap.dictObj.ResultList = e.DResult;
          } else if (e.Dtype === '134') {
            dataMap.dictObj.EnglishNameList = e.DResult;
          } else if (e.Dtype === '135') {
            dataMap.dictObj.EnglishSourceList = e.DResult;
          } else if (e.Dtype === '136') {
            dataMap.dictObj.PhoneSourceList = e.DResult;
          } else if (e.Dtype === '100') {
            dataMap.dictObj.ResultTypeList = e.DResult;
          }
        });
      }
    }
    //初始化对象属性
    const initializeProperty = (): void => {
      const keys: any[] = Object.keys(dataMap.source);
      keys.forEach(key => {
        dataMap.source[key+'showErr'] = ""
      });
    }
    //保存注册信息数据
    const save = (): void => {
      const keys: any[] = Object.keys(dataMap.source);
      keys.forEach(key => {
        const label = key+'Label'
        if(key.indexOf('Label') === -1){
          if(key.indexOf('showErr') === -1){
            checkRules(dataMap.source[key],dataMap.source[label],key)
          }
        }
      });
      return
      console.log('welcome..');
      const res: any = $api.report.saveEnterpriseRegisterInfo(dataMap.source)
    }
    const openPictureWindow = (): void => {
      console.log('open..');
      dataMap.dialogVisible = true;
    }
    const handleClose = (): void => {
      console.log('closeDialog..');
    }
    onBeforeMount(() => {
      initializeProperty(),
          getRegisterInfo(),
          getDictionary()
    })
    // 输入字段的校验
    const checkRules = (value: string,title: string,key: string): boolean => {
      if (value === '') {
        dataMap.source[key+'showErr'] = title+"不能为空"
        return false
      }else if(key === 'onlyCode'){
        if(value.length < 18){
          dataMap.source[key+'showErr'] = dataMap.source.onlyCodeLabel+"不能小于18位"
          return false
        }else {
          dataMap.source[key+'showErr'] = ''
        }
      }else if(key === 'registrationId'){
        if(value.length < 15){
          dataMap.source[key+'showErr'] = dataMap.source.registrationIdLabel+"不能小于15位"
          return false
        }else {
          dataMap.source[key+'showErr'] = ''
        }
      }else {
        dataMap.source[key+'showErr'] = ''
      }
      return true
    }
    //币种选择
    const changeMoneyType = (id: any): void => {
      dataMap.source.TotalType = id;
    }
    //经营期限
    const checkBusinessPeriodType = (id: any): void => {
      dataMap.source.businessPeriodType = id
      if (id != 9901) {
        dataMap.source.BusinessPeriod = [];
      }
    }
    //校验成立日期
    const checkRegistrationDate = (): void => {
      if (
          dataMap.source.businessPeriodType == 9901 &&
          dataMap.source.enterpriseType != '9907' &&
          dataMap.source.enterpriseType != '9908' &&
          dataMap.source.enterpriseType != '9909' &&
          dataMap.source.enterpriseType != '9911' &&
          dataMap.source.registrationDate != dataMap.source.BusinessPeriod[0]
      ) {
        dataMap.source['registrationDateshowErr'] = '成立日期与经营期限的开始时间不一致，请核对后输入';
      } else {
        dataMap.source['registrationDateshowErr'] = '';
      }
    }
    return {
      ...toRefs(dataMap),
      getRegisterInfo,
      save,
      openPictureWindow,
      handleClose,
      initializeProperty,
      getDictionary,
      changeMoneyType,
      checkBusinessPeriodType,
      checkRegistrationDate,
      checkRules,
    }
  }
})
