                        :class="{'danger': source.superiorAuthorityshowErr !== '' && source.superiorAuthorityshowErr !== undefined}"
                        @blur="checkRules(source.superiorAuthority,source.superiorAuthorityLabel,'superiorAuthority')"></el-input>
              <el-tooltip class="item" effect="dark" :content="source.superiorAuthorityshowErr" placement="top-start">
