            <span class="label">{{source.organizationCodeLabel}}</span>
            <div class="content content-vertical-box">
              <el-input v-model="source.organizationCode" :placeholder='source.organizationCodeLabel' ></el-input>
            </div>
          </div>
          <div class="pub-form-col-2 w100">
            <span class="label">{{source.commentLabel}}</span>
            <div class="content">
              <el-input v-model="source.comment" autosize type="textarea" :rows="2" :placeholder='source.commentLabel'></el-input>
              <i class="pub-form-icon el-icon-info add-icon-color"></i>
